	import LendCards from '../../components/LendCards.vue';
    import FooterMark from '../../components/FooterMark.vue';

	export default {
		components: {
			LendCards,
			FooterMark
		},
		data() {
			return {
				scrollLeft: 0,
				isClickChange: false,
				tabIndex: 1,
				newsitems: [],
				listData: [],
				newlistData:[],
				financeListData:[],
				transferListData:[],
				pageNo: '1',
				pageSize: '10',
				loadingText: '',
				tabBars: [{
					name: '金融智投',
					id: 'guanzhu'
				}, {
					name: '新标专区',
					id: 'tuijian'
				}, {
					name: '转让专区',
					id: 'tiyu'
				}]
			}
		},
		onLoad() {
			this.getListData()
		},
		methods: {
			goDetail(e) {
				uni.navigateTo({
					url: '/pages/lend/details'
				});
			},
			getListData(){
				const {pageNo, pageSize} = this
				this.request({
					url: 'transaction/projects/q',
					method: 'POST',
					params:{
						pageNo, pageSize
					}
				}).then(res =>{
					this.newlistData = this.newlistData.concat(res.data.result.content)
					this.listData = this.newlistData
					this.loadingText= ''
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '数据获取失败，重新登录！'
					});
				})
			},
			
			loadMore(e) {
				setTimeout(() => {
					this.addData(e);
				}, 1200);
			},
			addData(e) {
				if (this.listData.length > 30) {
					this.loadingText = '没有更多了';
					return;
				}
				this.pageNo ++
				this.loadingText = '加载中...'
				this.getListData()
			},
			async changeTab(e) {
				let index = e.target.current;
				if (this.isClickChange) {
					this.tabIndex = index;
					this.isClickChange = false;
					return;
				}
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft;
				let width = 0;

				for (let i = 0; i < index; i++) {
					let result = await this.getElSize(this.tabBars[i].id);
					width += result.width;
				}
				let winWidth = uni.getSystemInfoSync().windowWidth,
					nowElement = await this.getElSize(this.tabBars[index].id),
					nowWidth = nowElement.width;
				if (width + nowWidth - tabBarScrollLeft > winWidth) {
					this.scrollLeft = width + nowWidth - winWidth;
				}
				if (width < tabBarScrollLeft) {
					this.scrollLeft = width;
				}
				this.isClickChange = false;
				this.tabIndex = index;
				this.setData(index)
			},
			getElSize(id) { //得到元素的size
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data);
					}).exec();
				})
			},
			setData(index){
				if (index == 0) {
					this.listData = this.transferListData
				} else if (index == 1) {
					this.listData = this.newlistData
				} else {
					this.listData = this.financeListData
				}
			},
			async tapTab(e) { //点击tab-bar
				let tabIndex = e.target.dataset.current;
				if (this.tabIndex === tabIndex) {
					return false;
				} else {
					this.setData(tabIndex)
					let tabBar = await this.getElSize("tab-bar"),
						tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
					this.scrollLeft = tabBarScrollLeft;
					this.isClickChange = true;
					this.tabIndex = tabIndex;
				}
			},
		}
	}
